import User from './User'
import React from 'react'
import {Component} from 'react'
import axios from 'axios'
import storeFactory from '../store'
import {serverLink} from '../constants'
import HeadComp from './HeadComp'

export default class UserStore extends Component {

    state = {loading: 1}

    static async getInitialProps({ query: { id } }) {

        const {products, customerMeta, customerML} = await axios(serverLink + '/data/' + id)
        this.store = storeFactory({products, customerMeta, customerML})
        this.setState({loading: 0})
        return id
    }

    render() {

        return (
            <div>
                <HeadComp/>
                {!this.state.loading ? <User store={this.store}/> : <p/>}
            </div>
        )
    }
}


